import React, {useState} from 'react';
import {isEmptyString, toNormalDate} from "../js/utils";
import {TEXTS} from "../js/text";
import {Button, Card, Modal} from "react-bootstrap";
import {HIDDEN_KEYS, KEYS_DESCRIPTIONS} from "../js/refs";
import CardDetailedInfo from "./CardDetailedInfo";

const FOrg = React.memo((props) => {
    const [showFullAdditional1, setShowFullAdditional1] = useState(false);
    const [showModalExtendedInfo, setShowModalExtendedInfo] = useState(false);
    const closeExtendedInfoModal = () => {
        setShowModalExtendedInfo(false);
    }
    const checkKey = keyName => {
        if (orgInfo[keyName] === undefined || orgInfo[keyName] === null || !orgInfo[keyName].toString().trim().length) {
            return <i>{TEXTS.NO_DATA}</i>;
        }
        if (keyName !== 'agreementStartDate' && keyName !== 'agreementEndDate') {
            return orgInfo[keyName];
        }
        return toNormalDate(orgInfo[keyName]);
    }
    const {
        orgInfo,
        i
    } = props;
    const AGR_END_DATE = toNormalDate(orgInfo.agreementEndDate);
    const SHORT_ORG_NAME = orgInfo.orgName.split(',')[0].split('Адрес')[0];
    const keys = Object.entries(KEYS_DESCRIPTIONS).
        filter(key => !HIDDEN_KEYS.includes(key[0]));
    return (
        <tr>
            <td>
                {i + 1}
                <Button
                    variant="outline-primary"
                    onClick={() => setShowModalExtendedInfo(true)}
                    title={TEXTS.DETAILS}
                >
                    <i className="bi bi-eye"></i>
                </Button>
            </td>
            <td>
                {SHORT_ORG_NAME}
            </td>
            <td>
                {isEmptyString(AGR_END_DATE)
                    ? <i>{TEXTS.NO_DATA}</i>
                    : AGR_END_DATE
                }
            </td>
            <td>
                {isEmptyString(orgInfo.practicalTrainingAgreementNumber)
                    ? <i>{TEXTS.NO_DATA}</i>
                    : orgInfo.practicalTrainingAgreementNumber
                }
            </td>
            <td>
                {isEmptyString(orgInfo.practicalTrainingContract)
                    ? <i>{TEXTS.NO_DATA}</i>
                    : orgInfo.practicalTrainingContract
                }
            </td>
            <td>
                {orgInfo.additional1 != null && orgInfo.additional1.length > 170
                    ? showFullAdditional1
                        ? orgInfo.additional1
                        : <div
                            style={{cursor: 'pointer'}}
                            title={TEXTS.SHOW_FULL}
                            onClick={() => setShowFullAdditional1(true)}
                        >
                            {orgInfo.additional1.slice(0, 167) + '...'}
                        </div>
                    : orgInfo.additional1
                }
            </td>
            <CardDetailedInfo
                showModalExtendedInfo={showModalExtendedInfo}
                closeExtendedInfoModal={closeExtendedInfoModal}
                keys={keys}
                orgInfo={orgInfo}
                checkKey={checkKey}
            />
        </tr>
    );
});

export default FOrg;