import React from 'react';
import {Card, Container} from "react-bootstrap";
import MyNavbar from "../components/MyNavbar";
import {TEXTS, TITLES} from "../js/text";
import me from '../images/me.jpg';
import tg from '../images/tg.png';

const AboutMe = () => {
    document.title = TITLES.ABOUT_AUTHOR;
    return (
        <Container className="mt-3 text-center" fluid>
            <MyNavbar />
            <Card
                className="shadow-lg mt-5 d-flex flex-column justify-content-center"
                border="secondary"
            >
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.ABOUT_AUTHOR}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        <Card className="shadow-sm p-3 bg-light rounded-3 mt-3">
                            <Card.Body>
                                <Card.Title className="fs-4">
                                    Меня зовут Николай)
                                </Card.Title>
                                <Container>
                                    <img
                                        src={me}
                                        className="about-img"
                                    ></img>
                                </Container>
                                <Container className="justify fs-5 mt-3 indent">
                                    В данный момент обучаюсь на 2 курсе в УрГЭУ. Изначально я просто экспериментировал с GET/POST запросами, менял различные параметры веб-страниц, не особо понимая, как именно все это работает.
                                </Container>
                                <Container className="justify fs-5 indent">
                                    Программированием заинтересовался в 2020 году. В то время я выбирал, на каком языке писать код на ОГЭ по информатике. В итоге ОГЭ я писал на паскале (да, я существую), а JS изучал просто так, чтобы потыкаться. Параллельно с JS немного освоил HTML и CSS, тем самым расширив свои навыки.
                                </Container>
                                <Container className="justify fs-5 indent">
                                    В 2021 продолжал совершенствоваться, но по большей части в JS. В том же году узнал про существование расширений для браузеров, и с тех пор нет-нет, да напишу какое-нибудь расширение для улучшения своей жизни (иногда и жизни окружающих). :)
                                </Container>
                                <Container className="justify fs-5 indent">
                                    В 11 классе защищал индивидуальный проект, для которого сделал простенький конструктор тестов. На тот момент я познакомился с JQuery, и он мне очень помог в работе с DOM. В остальном, в 2022-2023 годах чего-либо особенного не было.
                                </Container>
                                <Container className="justify fs-5 indent">
                                    В 2024 году я наконец задумался о том, что пора бы уже осваивать те навыки, которые требуются Frontend-разработчику (перечень там большой). Собственно, в рамках этого я освоил на базовом уровне React, познакомился с популярными библиотеками (react-router-dom, axios и пр.), и поработал с bootstrap (все, что вы видите на этом сайте, — это он). Также немаловажным является тот факт, что я научился загружать свои проекты на github и смог найти подходящую БД для своего первого большого проекта.
                                </Container>
                                <Container className="justify fs-5 indent">
                                    Сейчас я осваиваю TypeScript, планирую писать на нем свои будущие проекты.
                                </Container>
                                <Container className="justify fs-5 indent">
                                    Связаться со мной можно в TG, или в ВК. ВК оставлять не буду — кто захочет, тот найдет.
                                </Container>
                                <img
                                    src={tg}
                                    style={{width: '300px', height: '300px', maxWidth: '100%'}}
                                ></img>
                            </Card.Body>
                        </Card>
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default AboutMe;