// routes.js

import Home from "../pages/Home";
import MLogin from "../pages/MLogin";
import MOrgs from "../pages/MOrgs";
import FindOrg from "../pages/FindOrgs";
import Instructions from "../pages/Instructions";
import FAQ from "../pages/FAQ";
import Guide from "../pages/Guide";
import AboutMe from "../pages/AboutMe";

export const routes = [
    {path: "/", element: <Home/> },
    {path: "/management/login", element: <MLogin/>},
    {path: "/management/orgs", element: <MOrgs/>},
    {path: "/findorg", element: <FindOrg/>},
    {path: "/guides", element: <Instructions/>},
    {path: "/showguide/:id/:cstep?", element: <Guide/>},
    {path: "/faq", element: <FAQ/>},
    {path: "/about", element: <AboutMe/>}
];

export default routes;
