import React, {useCallback, useState} from 'react';
import {Card, Container, Form} from "react-bootstrap";
import {TEXTS} from "../js/text";
import debounce from 'lodash.debounce';
import FOrgCard from "./FOrgCard";

const FOrgsListCard = (props) => {
    const {
        orgs,
        setOrgs,
        allOrgs,
        setAllOrgs,
        showAllOrgs
    } = props;
    const [searchOrgName, setSearchOrgName] = useState('');
    const handleSearchChange = useCallback(
        debounce((value) => {
            if (value.length > 0) {
                if (!showAllOrgs) {
                    setOrgs(allOrgs.filter(org => !org.isHidden).filter(org =>
                        org.orgName.toLowerCase().includes(value.toLowerCase())
                    ));
                } else {
                    setOrgs(allOrgs.filter(org =>
                        org.orgName.toLowerCase().includes(value.toLowerCase()))
                    )
                }
            } else if (value.length === 0) {
                if (!showAllOrgs) {
                    setOrgs(allOrgs.filter(org => !org.isHidden));
                } else {
                    setOrgs(allOrgs);
                }
            }
        }, 500), [allOrgs, setOrgs]
    );

    const onSearchChange = e => {
        const value = e.target.value;
        setSearchOrgName(value);
        handleSearchChange(value);
    };

    return (
        <Container>
            <Form.Control
                type="text"
                placeholder={TEXTS.SEARCH}
                value={searchOrgName}
                onChange={e => onSearchChange(e)}
            />
            {orgs.map((org, index) =>
                <FOrgCard
                    key={org.id}
                    orgInfo={org}
                    i={index}
                />
            )}
        </Container>
    );
};

export default FOrgsListCard;