import React from 'react';
import MyNavbar from "../components/MyNavbar";
import {Button, Card, Container} from "react-bootstrap";
import {TEXTS, TITLES} from "../js/text";
import {PRACTICE_GUIDES} from "../js/refs";
import {useNavigate} from "react-router-dom";
import GuideElement from "../components/GuideElement";

const Instructions = () => {
    document.title = TITLES.INSTRUCTIONS;
    const navigate = useNavigate();
    const nav = guideId => {
        navigate(`/showguide/${guideId}`);
    };

    return (
        <Container className="mt-3 text-center">
            <MyNavbar />
            <Card
                className="shadow-lg mt-5 d-flex flex-column justify-content-center"
                border="secondary"
            >
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.INSTRUCTIONS}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        <Card className="shadow-sm p-3 bg-light rounded-3">
                            <Card.Body>
                                {PRACTICE_GUIDES.map(guide =>
                                    <GuideElement
                                        key={guide.id}
                                        guide={guide}
                                        nav={nav}
                                    />
                                )}
                            </Card.Body>
                        </Card>
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Instructions;
