import {Button, Card} from "react-bootstrap";
import React from "react";
import '../styles/Steps.css';
import {scrollToTop} from "../js/utils";
import {TEXTS} from "../js/text";

const Steps = ({ activeStep, setActiveStep, steps, successMsg, isFinished, setIsFinished, children }) => {
    const finish = () => {
        setIsFinished(true);
    }
    const go = step => {
        scrollToTop();
        setActiveStep(activeStep => activeStep + step);
    }
    return (
        <div>
            {isFinished
                ? <div className="steps-container d-flex justify-content-center">
                    <Card>
                        <Card.Body>
                            {successMsg}
                            {children}
                        </Card.Body>
                    </Card>
                </div>
                :
                <div>
                    <div className="steps-container d-flex justify-content-center">
                        {steps.map((step, index) => (
                            <div key={index} className="step-item">
                                <div
                                    className={`step-circle ${
                                        index < activeStep
                                            ? "completed"
                                            : index === activeStep
                                                ? "active"
                                                : ""
                                    }`}
                                >
                                    {index + 1}
                                </div>
                                {index < steps.length - 1 && (
                                    <div
                                        className={`step-line ${
                                            index < activeStep
                                                ? "completed"
                                                : ""
                                        }`}
                                    ></div>
                                )}
                            </div>
                        ))}
                    </div>
                    <Card>
                        <Card.Body>
                            <Card.Title>
                                <h2>{steps[activeStep].TITLE}</h2>
                            </Card.Title>
                            {steps[activeStep].DESCRIPTION}
                        </Card.Body>
                    </Card>
                    <div className="text-center mt-4">
                        {activeStep !== 0 &&
                            <Button
                                className="me-3 fs-5"
                                onClick={() => go(-1)}
                            >
                                {TEXTS.BACK}
                            </Button>
                        }
                        {activeStep !== steps.length - 1
                            ? <Button
                                className="fs-5"
                                onClick={() => go(1)}
                            >
                                {TEXTS.NEXT}
                            </Button>
                            : <Button
                                className="fs-5"
                                variant="success"
                                onClick={finish}
                            >
                                {TEXTS.FINISH}
                            </Button>
                        }
                    </div>
                </div>
            }
        </div>
    );
};


export default Steps;