import React from 'react';
import MyNavbar from "../components/MyNavbar";
import {Card, Container} from "react-bootstrap";
import {FAQPage, TEXTS, TITLES} from "../js/text";
import {FAQ_LIST} from "../js/refs";
import FAQPart from "../components/FAQPart";
import FaqLinks from "../components/FAQLinks";
import FAQMistakes from "../components/FAQMistakes";

const FAQ = () => {
    document.title = TITLES.FAQ;
    return (
        <Container className="mt-3 text-center" fluid>
            <MyNavbar />
            <Card
                className="shadow-lg mt-5 d-flex flex-column justify-content-center"
                border="secondary"
            >
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.FAQ}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        <FAQPart
                            title={FAQPage.FAQ}
                        >
                            {FAQ_LIST.map(f =>
                                <details key={f.id}>
                                    <summary>
                                        <i className="bi bi-triangle custom-triangle"></i> {f.QUESTION}
                                    </summary>
                                    <div>
                                        {f.ANSWER}
                                    </div>
                                </details>
                            )}
                        </FAQPart>
                        <FAQPart
                            title={FAQPage.MISTAKES}
                        >
                            <FAQMistakes/>
                        </FAQPart>
                        <FAQPart
                            title={FAQPage.USEFUL_LINKS}
                        >
                            <FaqLinks/>
                        </FAQPart>
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default FAQ;