import React from 'react';
import {Container, Nav, Navbar} from "react-bootstrap";
import {TEXTS} from "../js/text.js";
import {useNavigate} from "react-router-dom";

const MyNavbar = () => {
    const navigate = useNavigate();
    return (
        <Navbar className="custom-navbar" variant="dark" expand="lg" fixed="top">
            <Container fluid className="shadow-sm">
                <Navbar.Brand href="#">
                    <i className="bi bi-send-fill"></i> {TEXTS.NAVBAR_BRAND}
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link className="ms-4" href="#FindOrg">
                            <i className="bi bi-search"></i> {TEXTS.FIND_ORG}
                        </Nav.Link>
                        <Nav.Link className="ms-4" href="#guides">
                            <i className="bi bi-file-earmark-text"></i> {TEXTS.INSTRUCTIONS}
                        </Nav.Link>
                        <Nav.Link className="ms-4" href="#faq">
                            <i className="bi bi-boxes"></i> {TEXTS.FAQ}
                        </Nav.Link>
                        <Nav.Link className="ms-4" href="#about">
                            <i className="bi bi-person"></i> {TEXTS.ABOUT_AUTHOR}
                        </Nav.Link>
                    </Nav>
                    <Nav className="ms-auto">
                        <Nav.Link
                            onClick={() => navigate('/management/login')}
                        >
                            <i className="bi bi-wrench-adjustable"></i> {TEXTS.MANAGEMENT}
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default MyNavbar;