import React from 'react';
import {Container} from "react-bootstrap";
import {TEXTS} from "../js/text";

const AboutProject = () => {
    return (
        <Container className="pt-5 mt-3">
            <h2>{TEXTS.ABOUT_PROJECT}</h2>
            <p className="justify fs-5">
                {TEXTS.PROJECT_INTRODUCTION}
            </p>
            <p className="justify fs-5">
                <i className="bi bi-search"></i>
                <b> {TEXTS.FIND_ORG}</b> {TEXTS.FIND_ORGS_DESCRIPTION}
            </p>
            <p className="justify fs-5">
                <i className="bi bi-file-earmark-text"></i>
                <b> {TEXTS.INSTRUCTIONS}</b> {TEXTS.INSTRUCTIONS_DESCRIPTION}
            </p>
            <p className="justify fs-5">
                <i className="bi bi-boxes"></i>
                <b> {TEXTS.FAQ}</b> {TEXTS.FAQ_DESCRIPTION}
            </p>
        </Container>
    );
};

export default AboutProject;