import React, { useEffect, useState } from 'react';
import { Alert, Button, Card, Container, Form, Spinner } from "react-bootstrap";
import { TEXTS, TITLES } from "../js/text";
import MyNavbar from "../components/MyNavbar";
import useFetching from "../hooks/useFetching";
import {getOrgs, scrollToTop} from "../js/utils";
import FOrgsList from "../components/FOrgsList";
import FOrgsListCard from "../components/FOrgsListCard";

const FindOrg = () => {
    document.title = TITLES.FIND_ORG;

    const [tableMode, setTableMode] = useState(true);
    const [showAllOrgs, setShowAllOrgs] = useState(false);
    const [filteredOrgs, setFilteredOrgs] = useState([]);
    const [orgs, setOrgs] = useState([]);
    const [itemsPerPage, setItemsPerPage] = useState(10); // Количество элементов на странице
    const [currentPage, setCurrentPage] = useState(1); // Текущая страница

    const fetchingCallback = async () => {
        const orgs = await getOrgs();
        setOrgs(orgs);
        // Начальное состояние filteredOrgs
        setFilteredOrgs(orgs.filter(org => !org.isHidden));
    };

    const [fetchOrgs, isLoading, error, statusMessage] = useFetching(fetchingCallback);

    useEffect(() => {
        fetchOrgs();
    }, []);

    // Обновляем список организаций на основе флага "Показывать все"
    useEffect(() => {
        if (showAllOrgs) {
            setFilteredOrgs([...orgs]); // Показываем все организации
        } else {
            setFilteredOrgs(orgs.filter(org => !org.isHidden)); // Показываем только видимые
        }
        setCurrentPage(1); // Сбрасываем текущую страницу на первую
    }, [showAllOrgs, orgs]); // Следим за изменениями showAllOrgs и orgs

    useEffect(() => {
        scrollToTop();
    }, [setCurrentPage, currentPage]);

    // Получаем данные для текущей страницы
    const getCurrentPageData = () => {
        if (itemsPerPage === "all") return filteredOrgs; // Если выбрано "все", возвращаем все записи
        const startIndex = (currentPage - 1) * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        return filteredOrgs.slice(startIndex, endIndex); // Вычисляем данные только для текущей страницы
    };

    // Вычисляем общее количество страниц
    const totalPages = itemsPerPage === "all" ? 1 : Math.ceil(filteredOrgs.length / itemsPerPage);

    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            <MyNavbar />
            <Card className="shadow-lg mt-5" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.ORGS}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        {isLoading && <Spinner animation="border" role="status" />}
                        {error && <Container>
                            <Alert variant="danger">{error}</Alert>
                            <Button
                                variant="secondary"
                                onClick={fetchOrgs}
                            >
                                {TEXTS.RETRY}
                            </Button>
                        </Container>}
                        {!isLoading && !error && statusMessage && statusMessage}
                    </Container>
                    {filteredOrgs.length >= 0 && (
                        <>
                            {/* Выбор количества элементов на странице */}
                            <Form.Group className="mb-3 d-flex justify-content-center align-items-center">
                                <Form.Label className="me-2">{TEXTS.ITEMS_PER_PAGE}</Form.Label>
                                <Form.Select
                                    value={itemsPerPage}
                                    onChange={(e) => setItemsPerPage(e.target.value === "all" ? "all" : parseInt(e.target.value))}
                                    style={{ width: "150px" }}
                                >
                                    <option value="10">10</option>
                                    <option value="25">25</option>
                                    <option value="50">50</option>
                                    <option value="all">Все</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="text-start fs-5">
                                <Form.Check
                                    type="checkbox"
                                    checked={showAllOrgs}
                                    onChange={e => setShowAllOrgs(e.target.checked)}
                                    label={TEXTS.SHOW_ALL_ORGS}
                                />
                            </Form.Group>
                            <Form.Group className="text-start fs-5">
                                <Form.Check
                                    type="checkbox"
                                    checked={tableMode}
                                    onChange={e => setTableMode(e.target.checked)}
                                    label={TEXTS.TABLE_MODE}
                                />
                            </Form.Group>
                            {/* Список элементов */}
                            {tableMode
                                ? <FOrgsList
                                    orgs={getCurrentPageData()} // Передаём данные для текущей страницы
                                    setOrgs={setFilteredOrgs}
                                    allOrgs={orgs}
                                    setAllOrgs={setOrgs}
                                    showAllOrgs={showAllOrgs}
                                />
                                : <FOrgsListCard
                                    orgs={getCurrentPageData()} // Передаём данные для текущей страницы
                                    setOrgs={setFilteredOrgs}
                                    allOrgs={orgs}
                                    setAllOrgs={setOrgs}
                                    showAllOrgs={showAllOrgs}
                                />
                            }

                            {/* Пагинация */}
                            {itemsPerPage !== "all" && totalPages > 1 && (
                                <div className="d-flex justify-content-center mt-3">
                                    {Array.from({ length: totalPages }, (_, i) => (
                                        <Button
                                            key={i}
                                            variant={i + 1 === currentPage ? "primary" : "secondary"}
                                            onClick={() => setCurrentPage(i + 1)}
                                            className="mx-1"
                                        >
                                            {i + 1}
                                        </Button>
                                    ))}
                                </div>
                            )}
                        </>
                    )}
                </Card.Body>
            </Card>
        </Container>
    );
};

export default FindOrg;
