import React from 'react';
import {Alert, Button, Card, Col, Container, Row} from "react-bootstrap";
import {TEXTS} from "../js/text";
import {useNavigate} from "react-router-dom";

const AccessDenied = () => {
    const navigate = useNavigate();
    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            <Card className="shadow-lg" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <Alert variant="danger" className="fs-1">
                            {TEXTS.ACCESS_DENIED}
                        </Alert>
                    </Card.Title>
                    <Alert variant="warning fs-5">
                        {TEXTS.YOU_ARENT_LOGGED_IN}
                    </Alert>
                    <Row>
                        <Col>
                            <Button
                                variant="outline-secondary fs-4"
                                onClick={() => navigate('/management/login')}
                            >
                                {TEXTS.AUTH}
                            </Button>
                        </Col>
                        <Col>
                            <Button
                                variant="primary fs-4"
                                onClick={() => navigate('/')}
                            >
                                {TEXTS.TO_THE_MAIN_PAGE}
                            </Button>
                        </Col>
                    </Row>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default AccessDenied;