import React, {useContext} from 'react';
import {Button, Container} from "react-bootstrap";
import MOrgsList from "../components/MOrgsList";
import {AppContext} from "../components/context";
import AccessDenied from "../components/AccessDenied";
import {useNavigate} from "react-router-dom";
import {TEXTS, TITLES} from "../js/text";

const MOrgs = () => {
    document.title = TITLES.ORGS;
    const {
        isAuth
    } = useContext(AppContext);
    const navigate =  useNavigate();
    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            {isAuth
                ? <Container>
                    <Button
                        variant="outline-secondary"
                        onClick={() => navigate('/')}
                    >{TEXTS.TO_THE_MAIN_PAGE}</Button>
                    <MOrgsList/>
                </Container>
                : <AccessDenied/>
            }
        </Container>
    );
};

export default MOrgs;