import React, {useContext, useEffect, useState} from 'react';
import {Alert, Button, Card, Container, Form, Row} from "react-bootstrap";
import {TEXTS, TITLES} from "../js/text";
import {AppContext} from "../components/context";
import {checkHash} from "../js/utils";
import {useNavigate} from "react-router-dom";

const MLogin = () => {
    document.title = TITLES.LOGIN;
    const navigate = useNavigate();
    const [p, setP] = useState('');
    const [sem, setSem] = useState(false);
    const {
        isAuth,
        setIsAuth
    } = useContext(AppContext);
    const login = async () => {
        if (!(await checkHash(p))) {
            setSem(true);
            return false;
        }
        setIsAuth(true);
        navigate('/management/orgs');
    }
    useEffect(() => {
        if (sem) {
            setSem(false);
        }
    }, [p, setP]);
    useEffect(() => {
        if (isAuth) {
            navigate('/management/orgs');
        }
    }, []);
    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            <Card className="shadow-lg" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.AUTH}</h1>
                    </Card.Title>
                    <Form className="p-5">
                        <Form.Group>
                            <Form.Control
                                value={p}
                                onChange={e => setP(e.target.value)}
                                size="lg"
                                type="password"
                                placeholder={TEXTS.PASSWORD}
                            ></Form.Control>
                        </Form.Group>
                    </Form>
                    {sem &&
                        <Alert variant="danger" className="transition">
                            {TEXTS.INCORRECT_PASSWORD}
                        </Alert>
                    }
                    <Button
                        variant="primary"
                        style={{ fontSize: '20px' }}
                        onClick={login}
                        disabled={!p.trim().length}
                    >
                        {TEXTS.LOGIN}
                    </Button>
                    <div className="mt-3">
                        <Button
                            variant="outline-warning"
                            style={{ fontSize: '20px' }}
                            onClick={() => navigate(-1)}
                        >
                            {TEXTS.BACK}
                        </Button>
                    </div>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default MLogin;