import React, {useCallback, useState} from 'react';
import {Form, Table} from "react-bootstrap";
import {KEYS_DESCRIPTIONS} from "../js/refs";
import {TEXTS} from "../js/text";
import FOrg from "./FOrg";
import debounce from 'lodash.debounce';

const FOrgsList = (props) => {
    const {
        orgs,
        setOrgs,
        allOrgs,
        showAllOrgs
    } = props;
    const [searchOrgName, setSearchOrgName] = useState('');
    const handleSearchChange = useCallback(
        debounce((value) => {
            if (value.length > 0) {
                if (!showAllOrgs) {
                    setOrgs(allOrgs.filter(org => !org.isHidden).filter(org =>
                        org.orgName.toLowerCase().includes(value.toLowerCase())
                    ));
                } else {
                    setOrgs(allOrgs.filter(org =>
                        org.orgName.toLowerCase().includes(value.toLowerCase()))
                    )
                }
            } else if (value.length === 0) {
                if (!showAllOrgs) {
                    setOrgs(allOrgs.filter(org => !org.isHidden));
                } else {
                    setOrgs(allOrgs);
                }
            }
        }, 500), [allOrgs, setOrgs]
    );

    const onSearchChange = e => {
        const value = e.target.value;
        setSearchOrgName(value);
        handleSearchChange(value);
    };
    return (
        <Table striped bordered hover responsive>
            <thead>
                <tr>
                    <th>№</th>
                    <th>
                        {KEYS_DESCRIPTIONS.orgName}
                        <Form.Control
                            type="text"
                            placeholder={TEXTS.SEARCH}
                            value={searchOrgName}
                            onChange={e => onSearchChange(e)}
                        />
                    </th>
                    <th>{KEYS_DESCRIPTIONS.agreementEndDate}</th>
                    <th>{KEYS_DESCRIPTIONS.practicalTrainingAgreementNumber}</th>
                    <th>{KEYS_DESCRIPTIONS.practicalTrainingContract}</th>
                    <th>{TEXTS.COMMENT}</th>
                </tr>
            </thead>
            <tbody>
                {orgs.map((org, index) =>
                    <FOrg key={org.id} orgInfo={org} i={index}/>
                )}
                {!orgs.length &&
                    <tr>
                        <td colSpan={6}>
                            {TEXTS.ORGS_NOT_FOUND}
                        </td>
                    </tr>
                }
            </tbody>
        </Table>
    );
};

export default FOrgsList;