import React from 'react';
import {Container} from "react-bootstrap";
import {TEXTS} from "../js/text";

const MyFooter = () => {
    return (
        <footer className="text-light py-1 fixed-bottom custom-footer shadow-lg">
            <Container>
                <p className="mb-0 text-center">{TEXTS.MY_APP_COPYRIGHT}</p>
            </Container>
        </footer>
    );
};

export default MyFooter;