import React, { useState } from 'react';
import { Modal, Button, Form } from "react-bootstrap";
import { TEXTS } from "../js/text";
import { addOrg } from "../js/utils";
import {KEYS_DESCRIPTIONS} from "../js/refs";

const MAddOrgModal = ({ show, handleClose, onOrgAdded }) => {
    const [orgName, setOrgName] = useState('');
    const [practicalTrainingAgreementNumber, setPracticalTrainingAgreementNumber] = useState('');
    const [practicalTrainingContract, setPracticalTrainingContract] = useState('');
    const [admissionDirections, setAdmissionDirections] = useState('');
    const [orgUrl, setOrgUrl] = useState('');
    const [additional1, setAdditional1] = useState('');
    const handleAddOrg = async () => {
        const newOrg = {
            orgName,
            practicalTrainingAgreementNumber,
            practicalTrainingContract,
            admissionDirections,
            additional1,
            orgUrl,
            agreementStartDate: null,
            agreementEndDate: null,
            isHidden: false,
        };
        try {
            const orgId = await addOrg(newOrg);
            onOrgAdded({ id: orgId, ...newOrg });
            handleClose();
        } catch (error) {
            console.error('Ошибка при добавлении организации:', error);
        }
    };

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>{TEXTS.ADD_ORG}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group controlId="orgName">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.orgName}
                            value={orgName}
                            onChange={e => setOrgName(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mt-2" controlId="practicalTrainingAgreementNumber">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.practicalTrainingAgreementNumber}
                            value={practicalTrainingAgreementNumber}
                            onChange={e => setPracticalTrainingAgreementNumber(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mt-2" controlId="practicalTrainingContract">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.practicalTrainingContract}
                            value={practicalTrainingContract}
                            onChange={e => setPracticalTrainingContract(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mt-2" controlId="admissionDirections">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.admissionDirections}
                            value={admissionDirections}
                            onChange={e => setAdmissionDirections(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mt-2" controlId="additional1">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.additional1}
                            value={additional1}
                            onChange={e => setAdditional1(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mt-2" controlId="orgUrl">
                        <Form.Control
                            type="text"
                            placeholder={KEYS_DESCRIPTIONS.orgUrl}
                            value={orgUrl}
                            onChange={e => setOrgUrl(e.target.value)}
                        />
                    </Form.Group>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    {TEXTS.CANCEL}
                </Button>
                <Button variant="primary" onClick={handleAddOrg} disabled={!orgName.trim()}>
                    {TEXTS.SAVE}
                </Button>
            </Modal.Footer>
        </Modal>
    );
};

export default MAddOrgModal;
