import React, { useState } from 'react';
import { Alert, Button } from 'react-bootstrap';
import '../styles/Alert.css';
import {TEXTS} from "../js/text"; // Подключение внешнего CSS для анимации

const DataCollectionAlert = () => {
    const [show, setShow] = useState(true);
    const handleClose = () => {
        setShow(false);
        storeDataInLocalStorage();
    };
    const storeDataInLocalStorage = () => {
        localStorage.setItem('dataCollection', '1');
    };
    return (
        <div>
            {show && (
                <Alert
                    variant="info"
                    onClose={handleClose}
                    className="alert-slide-in myAlert"
                >
                    <Alert.Heading>
                        {TEXTS.DATA_COLLECTION_ALERT_TITLE}
                    </Alert.Heading>
                    <p className="fs-5">
                        {TEXTS.DATA_COLLECTION_ALERT_TEXT}
                    </p>
                    <div className="d-flex justify-content-end">
                        <Button onClick={handleClose} variant="outline-secondary">
                            {TEXTS.CLEAR}
                        </Button>
                    </div>
                </Alert>
            )}
        </div>
    );
};

export default DataCollectionAlert;
