import React from 'react';
import {Button, Card} from "react-bootstrap";
import {TEXTS} from "../js/text";

const GuideElement = (props) => {
    const {
        guide,
        nav
    } = props;
    return (
        <Card key={guide.id} className="mt-3">
            <Card.Body>
                <Card.Title>
                    <h2 className="fs-3">
                        {guide.TITLE}
                    </h2>
                </Card.Title>
                <p className="fs-5">
                    {guide.DESCRIPTION}
                </p>
                <Button
                    className="mt-2"
                    onClick={() => nav(guide.id)}
                    title={TEXTS.SHOW_GUIDE}
                >
                    {TEXTS.OPEN}
                </Button>
            </Card.Body>
        </Card>
    );
};

export default GuideElement;