import React from 'react';
import {Card} from "react-bootstrap";

const FaqPart = ({ title, children }) => {
    return (
        <Card className="shadow-sm p-3 bg-light rounded-3 mt-3">
            <Card.Body className="justify">
                <h2>{title}</h2>
                {children}
            </Card.Body>
        </Card>
    );
};

export default FaqPart;