import React, {useState} from 'react';
import MOrgDisplay from "./MOrgDisplay";
import {editOrg, removeOrg, toNormalDate} from "../js/utils";

const MOrgLogic = (props) => {
    const {
        orgInfo,
        i,
        onOrgRemoved
    } = props;
    const [showEditModal, setShowEditModal] = useState(false);
    const [showOrgRemovedModal, setShowOrgRemovedModal] = useState(false);
    const [showAdmDir, setShowAdmDir] = useState(false);
    const [showModalRemoveOrg, setShowModalRemoveOrg] = useState(false);
    const [isFirstRender, setIsFirstRender] = useState(true);
    const [updatedOrg, setUpdatedOrg] = useState(orgInfo);

    const SHORT_ORG_NAME = (
        updatedOrg.orgName &&
        updatedOrg.orgName.split(',')[0]
    ) || '';

    const handleCheckboxChange = () => {
        const updatedValue = !updatedOrg.isHidden; // Предварительно переключаем значение
        setUpdatedOrg(prevOrg => ({
            ...prevOrg,
            isHidden: updatedValue
        }));
        editOrg(updatedOrg.id, { ...updatedOrg, isHidden: updatedValue })
            .then(() => {
                console.log("Organization updated successfully!");
            })
            .catch((error) => {
                console.error("Error updating organization:", error);
            });
    };


    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setUpdatedOrg(prevOrg => ({
            ...prevOrg,
            [name]: value
        }));
    };

    const handleSaveChanges = () => {
        editOrg(updatedOrg.id, updatedOrg)
            .then(() => {
                console.log("Organization updated successfully!");
                setShowEditModal(false);
            })
            .catch((error) => {
                console.error("Error updating organization:", error);
            });
        setShowEditModal(false);
    };

    const handleRemoveOrg = () => {
        removeOrg(updatedOrg.id)
            .then(() => {
                console.log("Organization removed successfully!");
                onOrgRemoved(updatedOrg.id); // Обновляем список организаций в родительском компоненте
                setShowModalRemoveOrg(false); // Закрываем модальное окно
                setShowOrgRemovedModal(true);
            })
            .catch((error) => {
                console.error("Error removing organization:", error);
            });
    };
    const normalEndDate = toNormalDate(updatedOrg.agreementEndDate);
    return (
        <MOrgDisplay
            orgInfo={orgInfo}
            i={i}
            onOrgRemoved={onOrgRemoved}
            setShowModalRemoveOrg={setShowModalRemoveOrg}
            setShowEditModal={setShowEditModal}
            SHORT_ORG_NAME={SHORT_ORG_NAME}
            updatedOrg={updatedOrg}
            normalEndDate={normalEndDate}
            setShowAdmDir={setShowAdmDir}
            admDir={showAdmDir}
            handleCheckboxChange={handleCheckboxChange}
            showAdmDir={showAdmDir}
            showModalRemoveOrg={showModalRemoveOrg}
            handleRemoveOrg={handleRemoveOrg}
            showOrgRemovedModal={showOrgRemovedModal}
            setShowOrgRemovedModal={setShowOrgRemovedModal}
            showEditModal={showEditModal}
            handleInputChange={handleInputChange}
            handleSaveChanges={handleSaveChanges}
        />
    );
};

export default MOrgLogic;