import React, {useState} from 'react';
import {useNavigate, useParams} from "react-router-dom";
import {PRACTICE_GUIDES} from "../js/refs";
import Steps from "../components/Steps";
import MyNavbar from "../components/MyNavbar";
import {Badge, Button, Card, Col, Container, Row} from "react-bootstrap";
import {TEXTS} from "../js/text";
import NotFound from "./NotFound";
import {checkStep, scrollToTop} from "../js/utils";

const Guide = () => {
    scrollToTop();
    const { id, cstep } = useParams();
    const [isFinished, setIsFinished] = useState(false);
    const navigate = useNavigate();
    const GUIDE = PRACTICE_GUIDES.find(g => g.id === id);
    const GUIDE_INDEX= PRACTICE_GUIDES.findIndex(g => g.id === id);
    const [activeStep, setActiveStep] = useState(
        checkStep(cstep, GUIDE)
    );
    const goBackInstructions = () => {
        navigate('/guides')
    }
    const goGuide = step => {
        setActiveStep(0);
        setIsFinished(false);
        navigate(`/showguide/${PRACTICE_GUIDES[GUIDE_INDEX + step].id}`);
    }
    if (GUIDE === undefined) {
        return <NotFound/>
    }
    document.title = `Практика | ${GUIDE.TITLE}`;
    return (
        <Container className="mt-3 text-center">
            <MyNavbar />
            <Card className="shadow-lg mt-5" border="secondary">
                <Card.Body>
                    {!isFinished &&
                        <div>
                            <Button
                                variant="outline-secondary"
                                onClick={goBackInstructions}
                            >{TEXTS.GO_BACK_INSTRUCTIONS}</Button>
                            <Card.Title>
                                <h1 className="mt-3">
                                    <Badge bg="success" className="text-wrap">
                                        {GUIDE.TITLE}
                                    </Badge>
                                </h1>
                            </Card.Title>
                        </div>
                    }
                    <Container className="mt-5">
                        <Steps
                            activeStep={activeStep}
                            setActiveStep={setActiveStep}
                            isFinished={isFinished}
                            setIsFinished={setIsFinished}
                            steps={GUIDE.STEPS}
                            successMsg={GUIDE.SUCCESS_MSG}
                            children={
                                <Container>
                                    <Row>
                                        <Col>
                                            <Button
                                                className="mt-3"
                                                variant="secondary"
                                                onClick={goBackInstructions}
                                            >{TEXTS.GO_BACK_INSTRUCTIONS}</Button>
                                        </Col>
                                    </Row>
                                    <Row className="mt-3">
                                        {GUIDE_INDEX !== 0 &&
                                            <Col>
                                                <Button
                                                    className="h-100 w-100 text-wrap"
                                                    variant="warning"
                                                    onClick={() => goGuide(-1)}
                                                ><i className="bi bi-arrow-90deg-left"></i> {PRACTICE_GUIDES[GUIDE_INDEX - 1].TITLE}</Button>
                                            </Col>
                                        }
                                        {GUIDE_INDEX !== PRACTICE_GUIDES.length - 1 &&
                                            <Col>
                                                <Button
                                                    className="h-100 w-100 text-wrap"
                                                    variant="success"
                                                    onClick={() => goGuide(1)}
                                                >{PRACTICE_GUIDES[GUIDE_INDEX + 1].TITLE} <i
                                                    className="bi bi-arrow-90deg-right"></i>
                                                </Button>
                                            </Col>
                                        }
                                    </Row>
                                </Container>
                            }
                        />
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Guide;