import React from 'react';
import {Button, Card, Modal} from "react-bootstrap";
import {TEXTS} from "../js/text";

const CardDetailedInfo = (props) => {
    const {
        showModalExtendedInfo,
        closeExtendedInfoModal,
        keys,
        orgInfo,
        checkKey
    } = props;
    return (
        <Modal show={showModalExtendedInfo} onHide={closeExtendedInfoModal}>
            <Modal.Header closeButton>
                <Modal.Title>{TEXTS.DETAILS}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {keys.map((key, index) =>
                        orgInfo[key[0]] != null
                        && orgInfo[key[0]].toString().trim().length > 0
                        && <Card className={index !== 0 && "mt-3"} key={key[0]}>
                            <Card.Body>
                                <Card.Title>
                                    {key[1]}
                                </Card.Title>
                                <div className="justify">
                                    {checkKey(key[0])}
                                </div>
                            </Card.Body>
                        </Card>
                )}
            </Modal.Body>
            <Modal.Footer>
                <Button
                    variant="secondary"
                    onClick={closeExtendedInfoModal}
                >
                    {TEXTS.CLOSE}
                </Button>
            </Modal.Footer>
        </Modal>
    );
};

export default CardDetailedInfo;