import React from 'react';
import {useNavigate} from "react-router-dom";
import {TEXTS, TITLES} from "../js/text";
import {Alert, Button, Card, Container} from "react-bootstrap";

const NotFound = () => {
    document.title = TITLES.NOT_FOUND;
    const navigate = useNavigate();
    const goBack = () => {
        navigate(-1);
    }
    return (
        <Container className="mt-5 text-center d-flex align-items-center justify-content-center">
            <Card>
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.ERR}</h1>
                    </Card.Title>
                    <div className="mt-4">
                        <Alert variant="danger">
                            <span className="fs-4">{TEXTS.ERR_NO_PAGE}</span>
                        </Alert>
                    </div>
                    <Button
                        variant="info"
                        style={{ fontSize: '20px' }}
                        onClick={goBack}
                    >
                        {TEXTS.BACK}
                    </Button>
                </Card.Body>
            </Card>
        </Container>

    );
};

export default NotFound;