import React from 'react';
import {MISTAKES} from "../js/refs";

const FaqMistakes = () => {
    return (
        <ul className="fs-5">
            {MISTAKES.map(m =>
                <li key={m.TEXT}>
                    {m.TEXT}
                </li>
            )}
        </ul>
    );
};

export default FaqMistakes;