import React from 'react';
import {USEFUL_LINKS} from "../js/refs";

const FaqLinks = () => {
    return (
        <ul className="fs-5">
            {USEFUL_LINKS.map(link =>
                <li key={link.TITLE}>
                    <a
                        href={link.LINK}
                        target="_blank" rel="noopener noreferrer"
                    >
                        {link.TITLE}
                    </a>
                </li>
                )}
        </ul>
    );
};

export default FaqLinks;