import { useState } from "react";
import {Alert} from "react-bootstrap";
import {TEXTS} from "../js/text";

/**
 * хук для обработки асинхронных запросов.
 * @param {Function} callback - асинхронная функция, которая будет выполняться.
 * @returns {[Function, boolean, string | null]} - [fetching, isLoading, error].
 */
const useFetching = (callback) => {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);
    const [statusMessage, setStatusMessage] = useState(null);

    const fetching = async (...args) => {
        setIsLoading(true);
        setError(null);
        setStatusMessage(<Alert variant="info">{TEXTS.READING_DATA}</Alert>);

        try {
            await callback(...args); // Если ошибка здесь, она попадет в блок catch
            setStatusMessage(<Alert variant="success">{TEXTS.DATA_RECEIVED}</Alert>);
        } catch (err) {
            const errorMessage = err.message || TEXTS.ERROR_REQUEST;
            setError(errorMessage);
            setStatusMessage(<Alert variant="danger">{errorMessage}</Alert>);
        } finally {
            setIsLoading(false);
        }
    };

    return [fetching, isLoading, error, statusMessage];
};

export default useFetching;
